
	import { 
		getCurrentInstance, 
		defineComponent, 
		onMounted, 
		ref,

	} from 'vue';

	import { useRoute } from 'vue-router';

	import { 
		IonApp, 
		IonModal, 
		IonContent, 
		IonList, 
		IonListHeader, 
		IonMenu, 
		IonNote, 
		IonRouterOutlet, 
		IonSplitPane,

	} from '@ionic/vue';
	
	import { 
		archiveOutline, 
		archiveSharp, 
		bookmarkOutline, 
		bookmarkSharp, 
		heartOutline, 
		heartSharp, 
		mailOutline, 
		mailSharp, 
		paperPlaneOutline, 
		paperPlaneSharp, 
		trashOutline, 
		trashSharp, 
		warningOutline, 
		warningSharp,

	} from 'ionicons/icons';

	import AuthModal from '@/components/AuthModal.vue';

	export default defineComponent({
		name: 'App',
		components: {
			IonApp, 
			IonModal,
			AuthModal,
			IonContent, 
			IonList, 
			IonListHeader, 
			IonMenu, 
			IonNote, 
			IonRouterOutlet, 
			IonSplitPane,
		},
		setup() {
			const route	    = useRoute();
			const isOpenRef = ref<boolean>(false);
			const vueApp    = getCurrentInstance();
			const eventBus  = vueApp?.appContext.config.globalProperties.eventBus;
			
			isOpenRef.value = localStorage.getItem('token') === null;
			
			const closeModal = (event: any) => {
				console.log('onCloseModal:', event);
				
				isOpenRef.value = false;
			};

			onMounted(() => {
				const theme = localStorage.getItem('theme') ?? 'light';
				const prefersDark = window.matchMedia('(prefers-color-scheme: dark)');
				const newColorScheme = prefersDark.matches ? "dark" : "light";
				
				console.log(newColorScheme, theme, prefersDark);
				
				localStorage.setItem('theme', theme);
				document.body.classList.toggle('dark', theme == 'dark');

				prefersDark.addEventListener('change', e => {
					const newColorScheme = e.matches ? "dark" : "light";

					localStorage.setItem('theme', newColorScheme);

					document.body.classList.toggle('dark', newColorScheme == 'dark');
				});

				eventBus.on('errorAuth', () => {
					console.log('errorAuth');

					isOpenRef.value = true;
				});
			});

			return { 
				isOpenRef,
				closeModal,
				archiveOutline, 
				archiveSharp, 
				bookmarkOutline, 
				bookmarkSharp, 
				heartOutline, 
				heartSharp, 
				mailOutline, 
				mailSharp, 
				paperPlaneOutline, 
				paperPlaneSharp, 
				trashOutline, 
				trashSharp, 
				warningOutline, 
				warningSharp,
				isSelected: (url: string) => url === route.path ? 'selected' : ''
			}
		}
	});
